@import url(https://fonts.googleapis.com/css2?family=Brygada+1918:ital@1&family=Lato:wght@300;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{font-size:14px;background-color:#f1f6f9;color:#14274e;box-sizing:border-box}main{display:flex;justify-content:center;align-items:center;flex-direction:column}button{color:#14274e;background-color:#f1f6f9;border:1px solid #14274e;border-radius:.5rem;transition:all 0.3s ease-out;font-family:"Lato",sans-serif;font-size:400;cursor:pointer;padding:1rem 0.5rem;display:flex;align-items:center}button:hover{box-shadow:0 14px 28px rgba(0,0,0,0.25),0 10px 10px rgba(0,0,0,0.22)}button svg{font-size:1.5rem;margin-left:0.5rem;color:#9a0680}.App{background-repeat:no-repeat;background-position:center right}.container{width:100%;display:block;box-sizing:border-box;margin-left:auto;margin-right:auto;padding-left:1rem;padding-right:1rem}.header{text-align:center;font-family:"Lato",sans-serif;font-size:1.5rem;font-weight:700;margin-bottom:4rem;letter-spacing:0.75rem}.header span{color:#9a0680}.card{background-color:#f1f6f9;border:1px solid #14274e;border-radius:.5rem;padding:2rem 1rem 0.5rem;box-sizing:border-box;width:70%;min-height:40vh;box-shadow:0 3px 6px rgba(0,0,0,0.16),0 3px 6px rgba(0,0,0,0.23);display:flex;flex-direction:column;justify-content:space-between}.author-wrapper{display:flex;flex-direction:column;align-items:flex-end}.quote{font-family:"Brygada 1918",serif;font-size:1.25rem;font-weight:400}.author{font-family:"Lato",sans-serif;font-size:1rem;font-weight:300;color:#14274e;-webkit-text-decoration:#9a0680 dashed underline;text-decoration:#9a0680 dashed underline;text-underline-offset:0.5rem;text-align:right}.button-box{margin-top:3rem;width:70%;display:flex;justify-content:space-between}.footer{margin-top:4rem;text-align:center;display:flex;justify-content:center;align-items:flex-end}@media screen and (max-width: 375px){.header{font-size:1rem}.button-box{width:100%}.card{width:100%}}

